import React, { useRef } from "react";
import { useGLTF, useHelper } from "@react-three/drei";
import { BoxHelper } from "three";
export function BoundingBox(props) {
  const { nodes, materials } = useGLTF("/bounding-box_adjusted-f.glb");
    const mesh = useRef();
    // useHelper(mesh, BoxHelper, 'black')
  return (
    <group {...props}  ref={mesh} dispose={null}>
       
       <lineSegments
        geometry={nodes.ObjNurb.geometry}
        material={materials.bounding}
        position={[4.15, 14.77, 8.7]}
      />
    </group>
  );
}

useGLTF.preload("/bounding-box_adjusted-f.glb");